import React from "react";
import { DateRangePicker } from "materialui-daterange-picker";
import Button from '../../components/UI/Button/Button';
import classes from './DateRangePicker.css';

const BasicDateRangePicker = (props) => {
    const [open, setOpen] = React.useState(false);
    const toggle = () => setOpen(!open);

    return (
        <div>
            <Button style={{ border: "1px solid green", borderRadius: "4px", marginLeft: "20px", padding: "5px" }} btnType="Success" clicked={toggle}>Filter By Date</Button>
            <DateRangePicker
                wrapperClassName={classes.DateRangePicker}
                open={open}
                toggle={toggle}
                onChange={props.onChange}
                maxDate={new Date()}
                initialDateRange={props.value}
            />
        </div>
    );
};

export default BasicDateRangePicker;



