import React, { Component } from 'react';
import { connect } from 'react-redux';

import Order from '../../components/Order/Order';
import axios from '../../axios-orders';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import * as actions from '../../store/actions/index';
import Spinner from '../../components/UI/Spinner/Spinner';
import BasicDateRangePicker from './DateRangePicker';

class Orders extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedRange: {
                startDate: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000),
                endDate: new Date()
            }
        }
    }

    componentDidMount() {
        this.props.onFetchOrders(this.props.token, this.props.userId);
    }

    setDateRange = (range) => {
        var d = new Date(range.startDate);
        d.setHours(0, 0, 0, 0);
        range.startDate = d
        this.setState({ selectedRange: range })
        console.log(range);
    }

    filterOrder = (order = []) => {
        let { selectedRange } = this.state;
        return order.filter(order => {
            let date = new Date(order.orderDate)
            return date >= selectedRange.startDate && date <= selectedRange.endDate
        })
    }

    render() {
        let orders = <Spinner />
        if (!this.props.loading) {
            orders = this.filterOrder(this.props.orders).map(order => (
                <div>
                    <Order
                        key={order.id}
                        ingredients={order.ingredients}
                        price={order.price}
                        date={order.orderDate} />
                </div>))
        }

        return (
            <div>
                <BasicDateRangePicker
                    onChange={this.setDateRange}
                    value={this.state.selectedRange}
                />
                <center>Your Orders</center>
                {orders}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        orders: state.order.orders,
        loading: state.order.loading,
        token: state.auth.token,
        userId: state.auth.userId
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onFetchOrders: (token, userId) => dispatch(actions.fetchOrders(token, userId))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(Orders, axios));


